import "./App.css";
import React, { useState, useEffect } from "react";
import { useReactMediaRecorder } from "react-media-recorder";
import ChartCard from "./components/ChartCard";
import { Chart as ChartJS } from "chart.js/auto";
import { CSVLink, CSVDownload } from "react-csv";
import { useTransition } from "react";

function App() {
  //const baseurl = "http://localhost:5000/";
  const baseurl = "";
  //const baseurl = "https://audioapp5.onrender.com";
  const [blob, setBlob] = useState();
  const { status, startRecording, stopRecording, mediaBlobUrl } =
    useReactMediaRecorder({ video: false });
  const [file, setFile] = useState();
  const [emotionsData, setEmotionsData] = useState("");
  const [sampleUploaded, setSampleUploaded] = useState(false);
  const [emotions, setEmotions] = useState("");
  const [gender, setGender] = useState("");
  const [isCheckedDensenet, setIsCheckedDensenet] = useState(true);
  const [isCheckedResnet, setIsCheckedResnet] = useState(true);
  const [isCheckedAlexnet, setIsCheckedAlexnet] = useState(true);
  const [showCharts, setShowCharts] = useState(true);
  const [youtubeLink, setYoutubeLink] = useState("");
  const [showLargeCharts, setShowLargeCharts] = useState(true);
  const [csv, setCsv] = useState(false);

  useEffect(() => {
    //handleCsvCreation();
    //console.log("useeffect");
  }, [emotionsData]);

  // Create CSV, needs some work...
  const handleCsvCreation = () => {
    let highest = -Infinity;
    let correctClass = null;
    let csvArr = ["name"];
    console.log("emotions loaded");
    if (emotionsData) {
      emotionsData.forEach((arr) => {
        arr.forEach((element) => {
          console.log(element);
          for (const val in element) {
            console.log(`${val}, ${element[val]}`);
            if (element[val] > highest) {
              highest = element[val];
              correctClass = val;
            }
          }
          highest = -Infinity;
          console.log("correctclass ", correctClass);
          csvArr.push(correctClass);
        });
      });
      console.log("csvarr", csvArr);
      let headers = [
        "File",
        "DenseNet121_Gender",
        "Resnet50_Gender",
        "AlexNet_Gender",
        "DenseNet121_Emotion",
        "Resnet50_Emotion",
        "AlexNet_Emotion",
      ];
      setCsv([[headers.slice()], csvArr.slice()]);
    }
  };

  const test_post = async () => {
    fetch(baseurl + "/test_post", { method: "POST", body: "aaa" })
      .then((res) => res.json())
      .then((res) => console.log(res))
      .catch((error) => console.log(error));
  };

  const test_get = async () => {
    fetch(baseurl + "/test_get", { method: "GET" })
      .then((res) => res.json())
      .then((res) => console.log(res))
      .catch((error) => console.log(error));
  };

  // AUDIO UPLOADING RELATED FUNCTIONS //

  // Upload youtube url
  const sendYoutubeLink = async () => {
    let link = "https://www.youtube.com/watch?v=QC8iQqtG0hg";

    console.log("youtube link ", link);
    fetch(baseurl + "/youtube", {
      method: "POST",
      body: youtubeLink,
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        setEmotionsData(data.values);
        setEmotions(data.values[0]);
        setGender(data.values[1]);
        setSampleUploaded(false);
      })
      .catch((error) => {
        console.log("error", error.message);
        setSampleUploaded(false);
        alert(error);
      });
  };

  // Upload recorded audio
  const sendWav = async () => {
    const mediaBlob = await fetch(mediaBlobUrl).then((response) =>
      response.blob()
    );
    const myFile = new File([mediaBlob], "file.wav", {
      type: useReactMediaRecorder.mimeType,
    });
    let fd = new FormData();
    fd.append("audiofile", mediaBlob);
    fd.append("file", "test");

    console.log(fd.get("audiofile"));
    console.log(fd.get("file"));
    setSampleUploaded(true);

    fetch(baseurl + "/blob", {
      method: "POST",
      body: fd,
    })
      .then((res) => res.json())
      .then((data) => {
        console.log("data values: ", data.values);
        console.log(data.values);
        setEmotionsData(data.values);
        setEmotions(data.values[0]);
        setGender(data.values[1]);
        setSampleUploaded(false);
      })
      .catch((error) => {
        setSampleUploaded(false);
        alert(error);
      });
  };

  // Upload audio file from computer
  const uploadAudioFile = (e) => {
    e.preventDefault();
    setSampleUploaded(true);
    console.log(file);
    let fd = new FormData();
    fd.append("file", file);

    fetch(baseurl + "/wav", {
      method: "POST",
      body: fd,
    })
      .then((res) => res.json())
      .then((data) => {
        console.log("data values", data.values);
        console.log("data values", data.values[0]);
        console.log("data values", data.values[0][0]);
        setEmotionsData(data.values);
        setEmotions(data.values[0]);
        setGender(data.values[1]);
        setSampleUploaded(false);
      })
      .catch((error) => {
        setSampleUploaded(false);
        alert("error occured");
      });
  };

  const handlefile = (e) => {
    console.log(e.target.files);
    setFile(e.target.files[0]);
  };

  return (
    <div className="App">
      <button onClick={() => test_post()}>POST</button>
      <button onClick={() => test_get()}>GET</button>
      <div className="container">
        <div className="div-grid">
          <div className="div-recording">
            <h1>Recording status: {status}</h1>
            <br />
            <div className="div-recording-buttons">
              <button onClick={startRecording}>Start Recording</button>
              <button onClick={stopRecording}>Stop Recording</button>
              {mediaBlobUrl == undefined ? null : (
                <>
                  <button onClick={() => sendWav()} type="submit">
                    Submit
                  </button>
                </>
              )}
              <audio src={mediaBlobUrl} controls />
            </div>
          </div>
          <div className="div-upload-audio">
            <form className="form-upload-audio">
              <label>Select file </label>
              <input
                onChange={(e) => {
                  handlefile(e);
                }}
                id="image"
                name="image"
                type="file"
              />
              <button onClick={(e) => uploadAudioFile(e)} type="submit">
                Submit file
              </button>
            </form>
          </div>
          <div className="div-youtube-upload">
            <input
              id="input-youtubetext"
              type="text"
              placeholder="Enter your youtube link here!"
              onChange={(e) => setYoutubeLink(e.target.value)}
            />
            <button
              onClick={() => {
                sendYoutubeLink();
              }}
              type="submit"
            >
              Submit
            </button>
            <button
              onClick={() => {
                sendYoutubeLink();
              }}
            >
              BUTTON
            </button>
          </div>
          <div
            className={
              sampleUploaded
                ? "div-uploaded-sample"
                : "div-uploaded-sample-hidden"
            }
          >
            {sampleUploaded ? (
              <h1>Analyzing your sample, please wait...</h1>
            ) : (
              <h1>ggg</h1>
            )}
          </div>

          {/* Settings div */}
          <div className="div-settings">
            <h2>Settings</h2>
            <hr />
            <form action="">
              <div>
                <input
                  checked={isCheckedDensenet}
                  onChange={() => setIsCheckedDensenet(!isCheckedDensenet)}
                  type="checkbox"
                  id="densenet"
                />
                <label for="densenet"> DenseNet-121 </label> <br />
              </div>
              <div>
                <input
                  checked={isCheckedResnet}
                  onChange={() => setIsCheckedResnet(!isCheckedResnet)}
                  type="checkbox"
                  id="resnet"
                />
                <label for="resnet"> Resnet-50 </label> <br />
              </div>
              <div>
                <input
                  checked={isCheckedAlexnet}
                  onChange={() => setIsCheckedAlexnet(!isCheckedAlexnet)}
                  type="checkbox"
                  id="alexnet"
                />
                <label for="showcharts"> Alexnet </label> <br />
              </div>
              <hr />
              <div>
                <input
                  checked={showCharts}
                  onChange={() => setShowCharts(!showCharts)}
                  type="checkbox"
                  id="showcharts"
                />
                <label for="toggleGraph"> Show graphs </label> <br />
              </div>
              <div>
                <input
                  checked={showLargeCharts}
                  onChange={() => setShowLargeCharts(!showLargeCharts)}
                  type="checkbox"
                  id="showLargeCharts"
                />
                <label for="showLargeCharts"> Large charts </label> <br />
              </div>
              <hr />
              <div>
                {csv ? (
                  <CSVLink
                    enclosingCharacter={``}
                    className="csv-download"
                    filename="csvfile"
                    target="_blank"
                    data={csv}
                  >
                    Download csv
                  </CSVLink>
                ) : null}
              </div>
            </form>
          </div>
        </div>
        <div
          className={
            showLargeCharts
              ? "div-classification-results"
              : "div-classification-results-small"
          }
        >
          <ChartCard
            className="div-chart div-chart-dense"
            header={"DenseNet-121"}
            emotionsData={gender[0]}
            ischecked={isCheckedDensenet}
            showCharts={showCharts}
            showLargeCharts={showLargeCharts}
            chartLabel="Gender classification"
            classes="gender"
          />
          <ChartCard
            className="div-chart div-chart-dense"
            header={"DenseNet-121"}
            emotionsData={emotions[0]}
            ischecked={isCheckedDensenet}
            showCharts={showCharts}
            showLargeCharts={showLargeCharts}
            chartLabel="Emotion classification"
            classes="emotion"
          />
          <ChartCard
            className="div-chart div-chart-resnet"
            header={"Resnet-50"}
            emotionsData={gender[1]}
            ischecked={isCheckedResnet}
            showCharts={showCharts}
            showLargeCharts={showLargeCharts}
            chartLabel="Gender classification"
            classes="gender"
          />
          <ChartCard
            className="div-chart div-chart-resnet"
            header={"Resnet-50"}
            emotionsData={emotions[1]}
            ischecked={isCheckedResnet}
            showCharts={showCharts}
            showLargeCharts={showLargeCharts}
            chartLabel="Emotion classification"
            classes="emotion"
          />
          <ChartCard
            className="div-chart div-chart-alexnet"
            header={"AlexNet"}
            emotionsData={gender[2]}
            ischecked={isCheckedAlexnet}
            showCharts={showCharts}
            showLargeCharts={showLargeCharts}
            chartLabel="Gender classification"
            classes="gender"
          />
          <ChartCard
            className="div-chart div-chart-alexnet"
            header={"AlexNet"}
            emotionsData={emotions[2]}
            ischecked={isCheckedAlexnet}
            showCharts={showCharts}
            showLargeCharts={showLargeCharts}
            chartLabel="Emotion classification"
            classes="emotion"
          />
        </div>
      </div>
    </div>
  );
}

export default App;
